import { Component, h, Host, Prop } from '@stencil/core';

export type HeaderSizes = 'h1' | 'h2' | 'h3' | 'h4' | 'h5' | 'h6';

/**
 * @category Layout
 * @description A header component for page and section titles with optional subtitle support. Provides consistent typography and spacing for content headers.
 * @slot title-prefix - Placed before the 'header_title'
 * @slot title-suffix - Placed after the 'header_title'
 * @slot custom-title - Used instead of the 'header_title' prop to place your own title elements
 * @slot subtitle-content - Used to place your own subtitle content
 * @slot actions - Placed at the bottom of the element for additional actions
 */
@Component({
    tag: 'at-header',
    shadow: false,
})
export class AtHeader {
    /**
     * Size of the header.
     */
    @Prop()
    size: HeaderSizes = 'h1';

    /**
     * Icon of header. Must use mat-icon name.
     */
    @Prop()
    icon?: string;

    /**
     * Title of the header.
     */
    @Prop()
    header_title?: string;

    /**
     * Subtitle of the header.
     */
    @Prop()
    subtitle?: string;

    /**
     * Adds a border to the bottom of the header.
     */
    @Prop()
    border?: boolean;

    /**
     * Adds 16 pixels of padding to the header element
     */
    @Prop()
    padding?: boolean = true;

    render() {
        const validHeadings: HeaderSizes[] = [
            'h1',
            'h2',
            'h3',
            'h4',
            'h5',
            'h6',
        ];
        const HeadingTag = validHeadings.includes(this.size)
            ? this.size
            : 'div';

        return (
            <Host
                class={`flex flex-row items-center justify-between overflow-hidden ${this.padding ? 'p-16' : ''} ${this.border ? 'border-light border-b' : ''}`}
            >
                <div class="flex items-center gap-8 overflow-hidden">
                    <slot name="title-prefix" />
                    <div class="flex flex-grow flex-col overflow-hidden">
                        <div
                            class={`${this.size} flex items-center`}
                            data-name="header-title-wrapper"
                        >
                            {this.header_title && (
                                <HeadingTag
                                    class="flex items-center truncate"
                                    data-name="header-title"
                                >
                                    {this.icon && (
                                        <i class="material-icons mr-8">
                                            {this.icon}
                                        </i>
                                    )}
                                    {this.header_title}
                                </HeadingTag>
                            )}
                            <slot name="title-suffix" />
                        </div>

                        <slot name="custom-title" />
                        {this.subtitle && (
                            <span
                                class="text-med truncate text-sm font-normal"
                                data-name="header-subtitle"
                            >
                                {this.subtitle}
                            </span>
                        )}
                        <slot name="subtitle-content" />
                    </div>
                </div>

                <div class={'flex items-center gap-8'}>
                    <slot name={'actions'} />
                </div>
            </Host>
        );
    }
}
